* {
    margin: 0;
    padding: 0;
}

.preloader__wrapper {
    background-color: #202020;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.preloader__item {
    display: block;
    position: relative;
    top: 50%;
    left: calc( 50% - 40px ); /*2 argument - size of preloader*/
}